@charset "UTF-8";
@import "icon", "mixin", "reset", "public","btn";
/* 轮播图 */
.index-banner {
  .swiper-pagination-bullet {
    opacity: 0.8;
    height: 10px;
    width: 10px;
    border: 2px solid #fff;
    background: none;
  }

  .swiper-pagination-bullet-active {
    background: #fff;
    border: 2px solid #fff;
    opacity: 1;
  }

  .swiper-pagination {
    bottom: 2px;
  }

  .swiper-slide {
    img {
      width: 100%;
      height: auto;
      display: block;
    }
  }
}

/* 轮播图 */
.bottom-banner {
  margin-top: 10px;
  background: #fff;
  .swiper-pagination-bullet {
    opacity: 0.8;
    height: 10px;
    width: 10px;
    border: 2px solid #fff;
    background: none;
  }

  .swiper-pagination-bullet-active {
    background: #fff;
    border: 2px solid #fff;
    opacity: 1;
  }

  .swiper-pagination {
    bottom: 2px;
  }

  .swiper-slide {
    img {
      width: 100%;
      height: auto;
      display: block;
    }
  }
}
/* 频道 */
.channel-nav {
  background: #fff;
  padding: 10px;
  margin-top: 10px;

  ul {
    li {
      float: left;
      width: 48%;
      margin: 1%;
      background: #ebf0f2;

      a {
        padding: 5px 10px;
        display: block;
      }

      h3 {
        color: #032b36;
        font-size: 14px;
        font-weight: 400;
        line-height: 20px;
      }

      p {
        font-size: 12px;
        color: #738897;
        line-height: 20px;
      }

      .pic {
        width: 50px;

        img {
          width: 50px;
          height: 50px;
          display: block;
          border-radius: 50px;
        }
      }
    }
  }
}
/* 快速投资 */

.fast-venturing {
  background: #fff;
  margin-top: 10px;
  padding: 10px;

  .bd {
    margin-top: 8px;

    .swiper-slide {
      border: 1px solid #ebf0f2;
      text-align: center;

      a {
        display: block;
        padding: 5px;
      }

      b,
      span {
        display: block;
      }

      b {
        color: #738897;
        font-weight: 400;
      }

      span {
        color: #eb5e3d;
      }
    }
  }
}
/*
理财精选 */
.financing-choice {
  background: #fff;
  margin-top: 10px;
  padding: 10px 0;
  padding-bottom: 0;

  .headline-h2 {
    padding: 0 10px 5px;
    border-bottom: 1px solid #ebf0f2;
  }

  .bd {
    padding: 0 10px;
    text-align: center;
    border-bottom: 1px solid #ebf0f2;

    a {
      padding: 10px 0;
      display: block;
    }

    h3 {
      font-weight: 400;
      font-size: 18px;
      line-height: 30px;
      color: #032b36;
    }

    h4 {
      font-size: 24px;
      font-weight: 400;
      line-height: 30px;
      color: $mainColor;

      span {
        font-size: 16px;
      }
    }

    h5 {
      color: #94c6e0;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      margin-bottom: 3px;
    }

    h6 {
      color: #6b8b91;
      font-size: 12px;
      font-weight: 400;
      line-height: 20px;
      padding-bottom: 5px;

      i {
        margin: 0 10px;
      }
    }
  }

  .ft {
    li {
      margin: 0 10px;
      overflow: hidden;
      border-bottom: 1px solid #ebf0f2;
      &:last-child{
        border-bottom: none;
      }
    }

    a {
      padding: 8px 0;
      display: flex;
    }

    .pic {
      width: 120px;
      margin-right: 10px;

      img {
        width: 120px;
        height: auto;
        display: block;
      }
    }

    .text {
      flex: 1;
    }

    h3 {
      color: #444;
      font-size: 16px;
      line-height: 22px;
      overflow: hidden;
    }

    h4 {
      color: #7b7b7b;
      font-size: 12px;
      line-height: 18px;
      height: 18px;
      overflow: hidden;
      font-weight: 400;
    }

    h5 {
      color: #22bae0;
      font-weight: 400;
      font-size: 16px;
    }
  }
}
