* {
  margin: 0;
  padding: 0;
  list-style: none;
  box-sizing: border-box; }

html {
  font-family: "Microsoft YaHei", Arial, Helvetica,"Hiragino Sans GB","Hiragino Sans GB W3", sans-serif;
  font-weight: 400;
  background: #fff;
  line-height: 1.438; }

body {
  font-size: 14px; }
  body a {
    text-decoration: none; }
    body a:hover {
