/* 轮播图 */
.index-banner .swiper-pagination-bullet {
  opacity: 0.8;
  height: 10px;
  width: 10px;
  border: 2px solid #fff;
  background: none; }

.index-banner .swiper-pagination-bullet-active {
  background: #fff;
  border: 2px solid #fff;
  opacity: 1; }

.index-banner .swiper-pagination {
  bottom: 2px; }

.index-banner .swiper-slide img {
  width: 100%;
  height: auto;
  display: block; }

/* 轮播图 */
.bottom-banner {
  margin-top: 10px;
  background: #fff; }
  .bottom-banner .swiper-pagination-bullet {
    opacity: 0.8;
