/* 频道 */
.channel-nav {
  background: #fff;
  padding: 10px;
  margin-top: 10px; }
  .channel-nav ul li {
    float: left;
    width: 48%;
    margin: 1%;
    background: #ebf0f2; }
    .channel-nav ul li a {
      padding: 5px 10px;
      display: block; }
    .channel-nav ul li h3 {
      color: #032b36;
      font-size: 14px;
      font-weight: 400;
      line-height: 20px; }
    .channel-nav ul li p {
      font-size: 12px;
      color: #738897;
      line-height: 20px; }
    .channel-nav ul li .pic {
      width: 50px; }
      .channel-nav ul li .pic img {
        width: 50px;
        height: 50px;
        display: block;
        border-radius: 50px; }

/* 快速投资 */
.fast-venturing {
  background: #fff;
  margin-top: 10px;
  padding: 10px; }
  .fast-venturing .bd {
    margin-top: 8px; }
    .fast-venturing .bd .swiper-slide {
      border: 1px solid #ebf0f2;
      text-align: center; }
      .fast-venturing .bd .swiper-slide a {
        display: block;
        padding: 5px; }
      .fast-venturing .bd .swiper-slide b,
      .fast-venturing .bd .swiper-slide span {
        display: block; }
      .fast-venturing .bd .swiper-slide b {
        color: #738897;
        font-weight: 400; }
      .fast-venturing .bd .swiper-slide span {
        color: #eb5e3d; }

/*
理财精选 */
.financing-choice {
  background: #fff;
  margin-top: 10px;
  padding: 10px 0;
  padding-bottom: 0; }
  .financing-choice .headline-h2 {
    padding: 0 10px 5px;
    border-bottom: 1px solid #ebf0f2; }
  .financing-choice .bd {
    padding: 0 10px;
    text-align: center;
    border-bottom: 1px solid #ebf0f2; }
    .financing-choice .bd a {
      padding: 10px 0;
      display: block; }
    .financing-choice .bd h3 {
      font-weight: 400;
      font-size: 18px;
      line-height: 30px;
      color: #032b36; }
    .financing-choice .bd h4 {
      font-size: 24px;
      font-weight: 400;
      line-height: 30px;
      color: #eb5e3d; }
      .financing-choice .bd h4 span {
        font-size: 16px; }
    .financing-choice .bd h5 {
      color: #94c6e0;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      margin-bottom: 3px; }
    .financing-choice .bd h6 {
      color: #6b8b91;
      font-size: 12px;
      font-weight: 400;
      line-height: 20px;
      padding-bottom: 5px; }
      .financing-choice .bd h6 i {
        margin: 0 10px; }
  .financing-choice .ft li {
    margin: 0 10px;
    overflow: hidden;
    border-bottom: 1px solid #ebf0f2; }
    .financing-choice .ft li:last-child {
      border-bottom: none; }
  .financing-choice .ft a {
    padding: 8px 0;
    display: flex; }
  .financing-choice .ft .pic {
    width: 120px;
    margin-right: 10px; }
    .financing-choice .ft .pic img {
      width: 120px;
      height: auto;
      display: block; }
  .financing-choice .ft .text {
    flex: 1; }
  .financing-choice .ft h3 {
    color: #444;
    font-size: 16px;
    line-height: 22px;
    overflow: hidden; }
  .financing-choice .ft h4 {
    color: #7b7b7b;
    font-size: 12px;
    line-height: 18px;
    height: 18px;
    overflow: hidden;
    font-weight: 400; }
  .financing-choice .ft h5 {
    color: #22bae0;
    font-weight: 400;
    font-size: 16px; }
